@import '../../variables.scss';

.top-bar {
  background: #484F51;
  height: $header-height;
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 5;
}

.logo-area {
  height: 100%;
  display: flex;
  align-items: center;
  text-decoration: none;
}

.user-area {
  display: flex;
  align-items: center;
}

.logo-img {
  height: 25px;
  margin-bottom: 2px;
  padding: 0px 8px 0px 12px;
}

.logo-text {
  font-size: 26px;
  margin-top: 2px;
  font-weight: 400;
  color: white;
  font-family: 'Rajdhani', sans-serif;
}

.search-box {
  height: 26px;
  border-radius: 5px;
  width: 300px;
  background: $highlight;
  padding-left: 8px;
  border: 1px solid rgba(42, 46, 48, 0.913);
}

::placeholder {
  font-style: italic;
}

.inner-search {
  width: 300px;
  position: relative;
  margin-right: 40px;
}

.fa-search {
  color: rgba(128, 128, 128, 0.619);
  position: absolute;
  top: 7px;
  right: 3px;
  font-size: 14px;
}

.user-links {
  display: flex;
  color: $highlight;
  margin-right: 20px;
}

.login-link {
  margin: 0 8px;
  padding: 0;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.79);
}

.login-link:hover {
  cursor: pointer;
  color: white;
}

.confirmation {
  padding: 20px 25px 10px 25px;
  width: 250px;
  font-size: 16px;
  line-height: 1.2;
  color: darkgreen;
}


